*{
  box-sizing: border-box;
}

body{
  background-color: #e14647;
  margin: 0;
}

h2{
  background-color:#e14647 ;
padding: 18px;
color: white;
font-size: 22pt;
text-align: center;
}

.app{
  padding: 30px;
  background: white;
  width: 80%;
  margin: 50px auto;
  box-shadow: 1px 2px 5px #eee;
}

input{
  
  width: 80%;
  border: 0;
  padding: .8em;
  background-color: #e8e8e8;
  margin-bottom: 2em;
}
button{

  width: 20%;
  border: 0;
  padding: .8em;
  margin: 0;
  outline: 0;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  background-color: #4dc55b;
}

ul{
  list-style: none;
  padding: 0;
}

li{
  margin: 15px 0;
  font-size: 2em;
}

li span{
  display: inline-block;
  width: 60%;
  color: #6a6a6a;
}
li>button:first-of-type{
  background-color:#6a6a6a ;
  color: white;
  margin-right: 1%;
  width: 19%;
}
li>button:last-of-type{
  background-color: #d9534f;
  color: white;
}
ul> form{
  margin: 15px 0;
}
